"use client";

import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { useRouter, useSearchParams } from "next/navigation";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { useAuth } from "@/context/AuthContext";
import {
    Crown,
    User,
    Lock,
    Mail,
    Eye,
    ArrowRight,
    LogIn
} from "lucide-react";

export default function AuthPage() {
    const router = useRouter();
    const searchParams = useSearchParams();
    const { login, register, loginAsGuest, isAuthenticated } = useAuth();

    const [isLogin, setIsLogin] = useState(true);
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState("");
    const [guestUsername, setGuestUsername] = useState("");

    // Form state
    const [formData, setFormData] = useState({
        username: "",
        email: "",
        password: "",
    });

    useEffect(() => {
        if (searchParams.get("mode") === "register") {
            setIsLogin(false);
        }
        if (searchParams.get("guest") === "true") {
            // Focus on guest section
        }
    }, [searchParams]);

    useEffect(() => {
        if (isAuthenticated) {
            router.push("/dashboard");
        }
    }, [isAuthenticated, router]);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError("");
        setIsLoading(true);

        try {
            let result;
            if (isLogin) {
                result = await login(formData.email, formData.password);
            } else {
                result = await register(formData.username, formData.email, formData.password);
            }

            if (!result.success) {
                setError(result.error || "Authentication failed");
            }
        } catch (err) {
            setError("An unexpected error occurred");
        } finally {
            setIsLoading(false);
        }
    };

    const handleGuestLogin = () => {
        if (guestUsername.trim().length < 2) {
            setError("Username must be at least 2 characters");
            return;
        }
        loginAsGuest(guestUsername.trim());
        router.push("/dashboard");
    };

    return (
        <div className="min-h-screen flex">
            {/* Left Panel - Member Access */}
            <div className="flex-1 bg-white p-8 md:p-16 flex flex-col justify-center">
                <motion.div
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    className="max-w-md mx-auto w-full"
                >
                    {/* Logo */}
                    <Link href="/" className="inline-flex items-center gap-2 mb-12">
                        <div className="w-8 h-8 bg-black rounded flex items-center justify-center">
                            <Crown className="w-5 h-5 text-white" />
                        </div>
                    </Link>

                    {/* Header */}
                    <h1 className="font-display text-4xl text-black mb-2">
                        MEMBER
                        <br />
                        ACCESS
                    </h1>
                    <p className="text-gray-500 text-sm tracking-widest mb-8">
                        SECURE TERMINAL // ENTER CREDENTIALS
                    </p>

                    {/* Toggle */}
                    <div className="flex gap-4 mb-8">
                        <button
                            onClick={() => setIsLogin(true)}
                            className={`text-sm font-medium pb-2 border-b-2 transition-colors ${isLogin
                                    ? "text-black border-black"
                                    : "text-gray-400 border-transparent hover:text-gray-600"
                                }`}
                        >
                            LOGIN
                        </button>
                        <button
                            onClick={() => setIsLogin(false)}
                            className={`text-sm font-medium pb-2 border-b-2 transition-colors ${!isLogin
                                    ? "text-black border-black"
                                    : "text-gray-400 border-transparent hover:text-gray-600"
                                }`}
                        >
                            REGISTER
                        </button>
                    </div>

                    {/* Form */}
                    <form onSubmit={handleSubmit} className="space-y-4">
                        {!isLogin && (
                            <div>
                                <label className="block text-xs text-gray-500 mb-2 tracking-widest">
                                    USERNAME / ID
                                </label>
                                <Input
                                    type="text"
                                    placeholder="AGENT_ID_00"
                                    value={formData.username}
                                    onChange={(e) => setFormData({ ...formData, username: e.target.value })}
                                    className="bg-gray-50 border-gray-200 text-black placeholder:text-gray-400"
                                    icon={<User className="w-4 h-4" />}
                                    required
                                />
                            </div>
                        )}

                        <div>
                            <label className="block text-xs text-gray-500 mb-2 tracking-widest">
                                {isLogin ? "USERNAME / ID" : "EMAIL"}
                            </label>
                            <Input
                                type={isLogin ? "text" : "email"}
                                placeholder={isLogin ? "AGENT_ID_00" : "agent@elite.chess"}
                                value={formData.email}
                                onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                                className="bg-gray-50 border-gray-200 text-black placeholder:text-gray-400"
                                icon={isLogin ? <User className="w-4 h-4" /> : <Mail className="w-4 h-4" />}
                                required
                            />
                        </div>

                        <div>
                            <label className="block text-xs text-gray-500 mb-2 tracking-widest">
                                PASSCODE
                            </label>
                            <Input
                                type="password"
                                placeholder="••••••••••••"
                                value={formData.password}
                                onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                                className="bg-gray-50 border-gray-200 text-black placeholder:text-gray-400"
                                icon={<Lock className="w-4 h-4" />}
                                required
                            />
                        </div>

                        {error && (
                            <motion.p
                                initial={{ opacity: 0, y: -10 }}
                                animate={{ opacity: 1, y: 0 }}
                                className="text-red-600 text-sm"
                            >
                                {error}
                            </motion.p>
                        )}

                        <Button
                            type="submit"
                            className="w-full bg-black hover:bg-gray-800 text-white"
                            size="lg"
                            disabled={isLoading}
                        >
                            {isLoading ? "AUTHENTICATING..." : "AUTHENTICATE"}
                            <ArrowRight className="w-4 h-4" />
                        </Button>
                    </form>

                    <div className="mt-6 flex items-center justify-between">
                        <button className="text-xs text-gray-500 hover:text-black underline">
                            FORGOT CREDENTIALS?
                        </button>
                        <span className="text-xs text-gray-400">V.2.0.4</span>
                    </div>

                    {/* Bottom bar */}
                    <div className="mt-12 pt-4 border-t border-gray-200">
                        <p className="text-xs text-gray-500">
                            NEW TO THE CLUB?{" "}
                            <button
                                onClick={() => setIsLogin(false)}
                                className="text-accent hover:underline font-medium"
                            >
                                SIGN UP NOW
                            </button>
                        </p>
                    </div>
                </motion.div>
            </div>

            {/* Right Panel - Visitor Pass (Guest) */}
            <div className="flex-1 bg-accent p-8 md:p-16 flex flex-col justify-center relative overflow-hidden">
                {/* Decorative background shapes */}
                <div className="absolute bottom-0 right-0 w-64 h-64 bg-red-500/20 transform rotate-45" />
                <div className="absolute bottom-20 right-20 w-32 h-32 bg-red-500/30 transform rotate-45" />

                <motion.div
                    initial={{ opacity: 0, x: 20 }}
                    animate={{ opacity: 1, x: 0 }}
                    className="max-w-md mx-auto w-full relative z-10"
                >
                    {/* Icon */}
                    <div className="w-16 h-16 bg-black rounded-full flex items-center justify-center mb-8">
                        <Eye className="w-8 h-8 text-white" />
                    </div>

                    {/* Header */}
                    <h2 className="font-display text-5xl text-white italic mb-4">
                        VISITOR
                        <br />
                        PASS
                    </h2>

                    {/* Description */}
                    <div className="flex items-start gap-2 mb-8">
                        <div className="w-1 h-16 bg-white/50" />
                        <p className="text-white/80">
                            Spectator mode enabled. No credentials required for limited access.
                        </p>
                    </div>

                    {/* Guest login */}
                    <div className="space-y-4">
                        <Input
                            type="text"
                            placeholder="Enter your callsign..."
                            value={guestUsername}
                            onChange={(e) => setGuestUsername(e.target.value)}
                            className="bg-white/10 border-white/30 text-white placeholder:text-white/50"
                        />

                        <Button
                            onClick={handleGuestLogin}
                            className="w-full bg-black hover:bg-gray-900 text-white"
                            size="lg"
                        >
                            CONTINUE AS GUEST
                            <LogIn className="w-4 h-4" />
                        </Button>
                    </div>

                    {/* Limitations notice */}
                    <div className="mt-8 p-4 bg-black/20 rounded">
                        <p className="text-xs text-white/60 uppercase tracking-wider mb-2">
                            Guest Limitations:
                        </p>
                        <ul className="text-sm text-white/80 space-y-1">
                            <li>• Casual games only</li>
                            <li>• No ELO ranking</li>
                            <li>• Limited match history</li>
                        </ul>
                    </div>
                </motion.div>
            </div>
        </div>
    );
}
